import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        tags: [], // 标签数据
        selectedTag: '',
        start: 0,
        end: 100,
    },
    mutations: {
        setTags(state, tags) {
            state.tags = tags;
        },
        setSelectedTag(state, tag) {
            console.log("在store设置标签" + tag);
            state.selectedTag = tag;
        },
    },
});

export default store;


